import { Link, Outlet } from 'react-router-dom';

const Layout = () => (
    <div>
        <ul>
            <li><Link to={process.env.PUBLIC_URL + "/foro/ver-hilos"}>Ver hilos</Link></li>
            <li><Link to={process.env.PUBLIC_URL + "/foro/crear-hilos"}>Crear hilos</Link></li>
        </ul>
        <Outlet />
    </div>
)

export default Layout;