import { BrowserRouter, Route, Routes } from 'react-router-dom';
import Home from '../../pages/home';
import Profile from '../../pages/profile';

const R = () => (
    <Routes>
        <Route path="/swapilibro" element={Home} />
        <Route path="/swapilibro/:id" element={Home} />

        <Route path="/swapilibro/profile/:id" element={Profile} />
        <Route path="*" component={() => <div>404 Swapi</div>} />
    </Routes>
);

export default R;
