import { Link, Outlet } from 'react-router-dom';

const NavBar = () => (
    <div>
        <div>
            <ul>
                <li><Link to={`${process.env.PUBLIC_URL}/hospital-nodejs/create`}>Create</Link></li>
                <li><Link to={`${process.env.PUBLIC_URL}/hospital-nodejs/read`}>Read</Link></li>
            </ul>
        </div>
        <Outlet />
    </div>
)

export default NavBar;