import { useEffect, useState } from 'react';
import { getPeople } from '../../services/services';
import { getIdFromUrl } from '../../application/utils';
import { Link } from 'react-router-dom';

const Home = () => {
    const [people, setPeople] = useState(null);

    useEffect(() => {
        getPeople().then((res) => {
            setPeople(res.data.results)
        });
    }, []);

    return (
        <div>
            {
                people && people.map((obj, i) => (
                    <Link to={"/swapi/profile/"} key={i}>
                        <img src={"https://starwars-visualguide.com/assets/img/characters/" + getIdFromUrl(obj.url) + ".jpg"} alt="imagen" />
                        <p>{obj.name}</p>
                    </Link>)
                )
            }
        </div>
    );
};

export default Home;