import { Link, Outlet } from 'react-router-dom';

const Layout = () => (
    <div>
        <div>
            <ul>
                <li><Link to={process.env.PUBLIC_URL + "/foro-nodejs/"}>Home</Link></li>
                <li><Link to={process.env.PUBLIC_URL + "/foro-nodejs/see-threads"}>Ver hilos</Link></li>
                <li><Link to={process.env.PUBLIC_URL + "/foro-nodejs/write-thread"}>Crear nuevo hilo</Link></li>
            </ul>
        </div>
        <Outlet />
    </div>
)

export default Layout;