import { useState, useRef } from 'react';

const App = () => {
    const centigradesRef = useRef();
    const [fahrenheit, setFahrenheit] = useState(0);

    const calcular = () => {
        const fahrenheitValue = centigradesRef.current.value * 9 / 5 + 32;
        setFahrenheit(fahrenheitValue);
    }

    return (
        <div>
            <input type="text" ref={centigradesRef} />
            <button onClick={() => calcular()}>Enviar</button>
            {fahrenheit}
        </div>
    );
};

export default App;