import { useState } from 'react';
import Linea from './linea';
import txts from './txts.json';
import { Row, Button } from './styled';
const App = () => {
    const [currentActive, setCurrentActive] = useState(0);
    return (
        <div>
            <Row>
                <Button onClick={() => setCurrentActive(currentActive > 0 ? currentActive - 1 : currentActive)}>Anterior</Button>
                <Button onClick={() => setCurrentActive(currentActive < txts.length - 1 ? currentActive + 1 : currentActive + 1)}>Siguiente</Button>
            </Row>
            {
                txts.map((txt, i) => <Linea isActive={i === currentActive} txt={txt} />)
            }
        </div>
    );
};

export default App;