import { useSelector, useDispatch } from "react-redux";
import { setName } from "../redux/states/user/reducer";
import { selectUserName } from "../redux/states/user/selecters";

const App = () => {
    const name = useSelector(selectUserName); // useSelector me devuelve el estado de toda la aplicacin. Llamaremos al mtodo selectUserName para acceder concretamente a la propiedad de ese estado que nos interesa
    const dispatch = useDispatch(); // el mtodo dispatch recibe un objeto action. Dicho objeto ser pasado a todos los reducers para ver con cual coincide el type y en funcin de eso, modificar el state
    // un objeto action es aquel que tiene como propiedades un type y un payload

    const onChange = ({ target }) => {
        // console.log(setName(target.value));
        const objAction = setName(target.value);
        dispatch(objAction);
    }

    return (
        <div>
            <p>{name}</p>
            <input type="text" onChange={onChange} />
        </div>
    )
}

export default App;