import { useState } from 'react';
import CosteHotel from './componentes/CosteHotel';
import CosteAlquiler from './componentes/CosteAlquiler';

const App = () => {

    const [noches, setNoches] = useState(0);
    return <div>
        <input type="text" onChange={obj => setNoches(obj.target.value)} />
        <CosteHotel noches={noches} />
        <CosteAlquiler noches={noches} />
    </div>;
};

export default App;