import { Link, Outlet } from 'react-router-dom';

const Layout = () => (
    <div>
        <div>
            <ul>
                <li><Link to={process.env.PUBLIC_URL + "/mensajeria-nodejs/"}>Home</Link></li>
                <li><Link to={process.env.PUBLIC_URL + "/mensajeria-nodejs/seeMessages"}>See messages</Link></li>
                <li><Link to={process.env.PUBLIC_URL + "/mensajeria-nodejs/writeMessage"}>Write message</Link></li>
            </ul>
        </div>
        <Outlet />
    </div>
)

export default Layout;