import { useEffect, useState, useContext } from 'react';
import { getUser, newTask } from '../app/services/users';
import { AppContext } from '../app/Provider';
import Task from '../components/Task';

const Tasks = () => {
    const [tasks, setTasks] = useState([]);
    const [taskText, setTaskText] = useState('');
    const [state, setState] = useContext(AppContext);

    const getTasks = () => getUser(state.id).then(u => {
        u.tasks && setTasks(u.tasks);
    });

    useEffect(() => {
        getTasks()
    }, []);

    return (
        <div>
            <h1>Tasks</h1>
            <div>
                {
                    tasks.map((task, i) => <Task txt={task.txt} i={i} userId={state.id} getTasks={getTasks} />)
                }
            </div>

            <textarea onChange={(e) => setTaskText(e.target.value)}></textarea>
            <p><button onClick={async () => {
                await newTask(state.id, taskText)
                getTasks();
            }}>Aadir tarea</button></p>
        </div>
    )
}

export default Tasks;